* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.Blog {
  height: 100vh;
}
.Blog h1 {
  text-align: center;
  text-transform: uppercase;
  color: darkgray;
  margin-bottom: 4rem;
}

.Blog-box {
  display: flex;
  width: 40%;
  background-color: #eee;
  display: block;
  margin-left: 6rem;
  border-radius: 10px;
  float: left;
  padding: 0 2rem;
  margin-bottom: 2rem;
}
.Blog-box h1 {
  border-bottom: 1px solid darkgray;
}
p {
  height: 1%;
  margin-top: 1rem;
  margin-bottom: 12rem;
}

a:not(:last-of-type) {
  margin-left: 83%;
}
a {
  margin-top: -4rem;
}
